<template>
    <div class="job-profile-details">
        <!-- 调试信息（发布时删除） -->
        <div v-if="false" style="position: absolute; top: 0; left: 0; background: rgba(0,0,0,0.8); color: #fff; padding: 5px; font-size: 10px; z-index: 999;">
            avgSalary原始值: {{ data?.avgSalary }}
        </div>
        
        <div class="stats-panel">
            <div class="stat-item">
                <div class="stat-value" :key="statKey">{{ totalJobsDisplay }}</div>
                <div class="stat-label">总职位数</div>
            </div>
            <div class="stat-item">
                <div class="stat-value" :key="statKey + '_salary'">{{ avgSalaryDisplay }}</div>
                <div class="stat-label">平均薪资</div>
            </div>
        </div>
        
        <div class="profile-summary">
            <div class="summary-title">热门职位排名</div>
            <div class="job-list" id="job-list-scroll">
                <div v-if="!hasData" class="no-data">
                    数据加载中...
                </div>
                <div v-else v-for="(profile, index) in profiles" :key="index" 
                     class="job-item" :class="{'active-job': activeJobIndex === index}"
                     @mouseenter="activeJobIndex = index" @mouseleave="activeJobIndex = -1">
                    <span class="job-rank" :style="{background: getRankColor(index)}">{{ index + 1 }}</span>
                    <div class="job-info">
                        <span class="job-title" :title="profile.title">{{ profile.title }}</span>
                        <div class="job-details" v-show="activeJobIndex === index">
                            <div class="detail-row">
                                <span class="detail-label">平均薪资:</span>
                                <span class="detail-value">{{ profile.avgSalary }}</span>
                            </div>
                            <div class="detail-row">
                                <span class="detail-label">招聘数量:</span>
                                <span class="detail-value">{{ profile.count }}个</span>
                            </div>
                        </div>
                    </div>
                    <span class="job-count">{{ profile.count }}个</span>
                </div>
            </div>
        </div>
        
        <div class="industry-city-panel">
            <div class="panel-item">
                <div class="panel-title">主要行业分布</div>
                <div class="panel-content" id="industry-list-scroll">
                    <div v-if="!hasData" class="no-data">
                        数据加载中...
                    </div>
                    <div v-else v-for="(industry, index) in topIndustries" 
                         :key="'ind-'+index" class="tag-item"
                         :style="{borderLeft: '3px solid ' + getTagColor(index)}">
                        <span class="tag-name" :title="industry.name">{{ industry.name }}</span>
                        <span class="tag-count">{{ industry.value }}</span>
                    </div>
                </div>
            </div>
            <div class="panel-item">
                <div class="panel-title">热门城市分布</div>
                <div class="panel-content" id="city-list-scroll">
                    <div v-if="!hasData" class="no-data">
                        数据加载中...
                    </div>
                    <div v-else v-for="(city, index) in topCities" 
                         :key="'city-'+index" class="tag-item"
                         :style="{borderLeft: '3px solid ' + getTagColor(index + 5)}">
                        <span class="tag-name" :title="city.name">{{ city.name }}</span>
                        <span class="tag-count">{{ city.value }}</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'JobProfileDetails',
    props: {
        data: {
            type: Object,
            default: null
        }
    },
    data() {
        return {
            colors: ["#2c7bfe", "#c2232a", "#feed2c", "#a262f2", "#62d5f2", "#fe672c", "#69f262", "#2ca8fe", "#fc54a6", "#7e57c2"],
            scrollIntervals: {},
            activeJobIndex: -1,
            statKey: 0
        }
    },
    computed: {
        hasData() {
            return this.data && this.data.profiles && this.data.profiles.length > 0;
        },
        profiles() {
            return this.data?.profiles || [];
        },
        totalJobsDisplay() {
            const total = this.data?.totalJobs || 0;
            console.log('总职位数显示值:', total);
            return total;
        },
        avgSalaryDisplay() {
            const avgSalary = this.data?.avgSalary || '0K';
            console.log('平均薪资显示值:', avgSalary);
            return avgSalary;
        },
        topIndustries() {
            return this.data?.topIndustries || [];
        },
        topCities() {
            return this.data?.topCities || [];
        }
    },
    watch: {
        data: {
            handler(newVal) {
                console.log('JobProfileDetails 接收到数据:', newVal);
                if (newVal) {
                    console.log('总职位数:', newVal.totalJobs);
                    console.log('平均薪资:', newVal.avgSalary);
                    // 触发动画更新
                    this.statKey++;
                    this.$nextTick(() => {
                        this.setupScrolling();
                        this.animateStats();
                    });
                }
            },
            deep: true,
            immediate: true
        }
    },
    methods: {
        setChart() {
            // 设置垂直滚动列表
            this.setupScrolling();
            
            // 添加统计数据动画
            if (this.hasData) {
                this.animateStats();
            }
        },

        setupScrolling() {
            // 清除之前的滚动间隔
            Object.values(this.scrollIntervals).forEach(interval => clearInterval(interval));
            this.scrollIntervals = {};
            
            if (!this.hasData) return;
            
            // 设置自动滚动，确保不会溢出边界
            this.scrollIntervals.jobs = this.scrollElement('job-list-scroll', 30);
            this.scrollIntervals.industries = this.scrollElement('industry-list-scroll', 40);
            this.scrollIntervals.cities = this.scrollElement('city-list-scroll', 40);
        },

        scrollElement(elementId, delay) {
            const element = document.getElementById(elementId);
            if (element) {
                // 只有当内容超出容器高度时才滚动
                if (element.scrollHeight > element.clientHeight) {
                    let scrollPos = 0;
                    let direction = 1;
                    let paused = false;
                    
                    const interval = setInterval(() => {
                        if (paused) return;
                        
                        if (direction > 0) {
                            // 向下滚动
                            scrollPos += 0.5;
                            if (scrollPos >= element.scrollHeight - element.clientHeight) {
                                // 到底了，等待一段时间后反向
                                setTimeout(() => {
                                    direction = -1;
                                }, 2000);
                            }
                        } else {
                            // 向上滚动
                            scrollPos -= 0.5;
                            if (scrollPos <= 0) {
                                // 到顶了，等待一段时间后反向
                                setTimeout(() => {
                                    direction = 1;
                                }, 2000);
                            }
                        }
                        element.scrollTop = scrollPos;
                    }, delay);

                    // 当鼠标悬停时暂停滚动
                    element.addEventListener('mouseenter', () => {
                        paused = true;
                    });

                    element.addEventListener('mouseleave', () => {
                        paused = false;
                    });
                    
                    return interval;
                }
            }
            return null;
        },
        
        animateStats() {
            // 添加数字增长动画效果
            const statValues = this.$el.querySelectorAll('.stat-value');
            
            console.log('开始动画，找到的元素:', statValues.length);
            
            statValues.forEach((el, index) => {
                let finalValue = el.textContent;
                console.log(`元素${index}的值:`, finalValue);
                
                // 判断是否是数字（考虑带K的情况）
                let isNumeric = false;
                let targetValue = 0;
                let suffix = '';
                
                // 处理带K的数字（如薪资）
                if (finalValue.includes('K')) {
                    const numPart = finalValue.replace('K', '');
                    if (!isNaN(parseFloat(numPart))) {
                        isNumeric = true;
                        targetValue = parseFloat(numPart);
                        suffix = 'K';
                    }
                } else if (!isNaN(parseFloat(finalValue))) {
                    // 处理纯数字
                    isNumeric = true;
                    targetValue = parseFloat(finalValue);
                    suffix = '';
                }
                
                console.log(`处理结果 - isNumeric: ${isNumeric}, targetValue: ${targetValue}, suffix: ${suffix}`);
                
                // 如果是0K，跳过动画直接显示
                if (finalValue === '0K') {
                    console.log('薪资为0K，跳过动画');
                    return;
                }
                
                if (isNumeric && targetValue > 0) {
                    let startValue = 0;
                    let duration = 1500; // 动画持续时间(ms)
                    let startTime = null;
                    
                    const animateNumber = (timestamp) => {
                        if (!startTime) startTime = timestamp;
                        let progress = timestamp - startTime;
                        let percentage = Math.min(progress / duration, 1);
                        
                        // 使用缓动函数使动画更平滑
                        let easeOutCubic = 1 - Math.pow(1 - percentage, 3);
                        let currentValue;
                        
                        // 如果是薪资（带小数），保留小数
                        if (suffix === 'K' && targetValue % 1 !== 0) {
                            currentValue = (startValue + (targetValue - startValue) * easeOutCubic).toFixed(1);
                        } else {
                            currentValue = Math.floor(startValue + (targetValue - startValue) * easeOutCubic);
                        }
                        
                        el.textContent = currentValue + suffix;
                        
                        if (percentage < 1) {
                            requestAnimationFrame(animateNumber);
                        } else {
                            el.textContent = finalValue;
                        }
                    };
                    
                    // 设置起始值
                    el.textContent = '0' + suffix;
                    
                    // 延迟启动动画
                    setTimeout(() => {
                        requestAnimationFrame(animateNumber);
                    }, index * 200); // 分别延迟启动每个数字动画
                }
            });
        },
        
        getRankColor(index) {
            return this.colors[index % this.colors.length];
        },
        
        getTagColor(index) {
            return this.colors[index % this.colors.length];
        }
    },
    mounted() {
        this.setChart();
    },
    beforeDestroy() {
        // 清除所有滚动间隔
        Object.values(this.scrollIntervals).forEach(interval => {
            if (interval) clearInterval(interval);
        });
    }
}
</script>

<style lang="less" scoped>
.job-profile-details {
    width: 100%;
    height: 100%;
    padding: 5px;
    color: #75deef;
    display: flex;
    flex-direction: column;
    
    .no-data {
        text-align: center;
        color: #75deef;
        padding: 20px;
        font-size: 14px;
    }
    
    .stats-panel {
        display: flex;
        justify-content: space-between;
        margin-bottom: 8px;
        
        .stat-item {
            text-align: center;
            background: rgba(3, 4, 74, 0.5);
            border: 1px solid #0D2451;
            border-radius: 4px;
            padding: 5px 8px;
            width: 48%;
            position: relative;
            overflow: hidden;
            box-shadow: 0 0 10px rgba(13, 36, 81, 0.5) inset;
            
            &:before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 2px;
                background: linear-gradient(to right, transparent, #2ca8fe, transparent);
                animation: stat-border-flow 3s infinite;
            }
            
            .stat-value {
                font-size: 22px;
                font-weight: bold;
                color: #feed2c;
                text-shadow: 0 0 8px rgba(254, 237, 44, 0.5);
                transition: all 0.3s ease;
                min-height: 30px;
                line-height: 30px;
            }
            
            .stat-label {
                font-size: 12px;
                margin-top: 2px;
                color: #75deef;
            }
            
            &:hover .stat-value {
                transform: scale(1.1);
            }
        }
    }
    
    .profile-summary {
        flex: 1;
        margin-bottom: 8px;
        max-height: 35%;
        
        .summary-title {
            font-size: 14px;
            margin-bottom: 5px;
            color: #75deef;
            display: flex;
            align-items: center;
            
            &:before {
                content: '';
                display: inline-block;
                width: 3px;
                height: 14px;
                background: linear-gradient(to bottom, #75deef, #2ca8fe);
                margin-right: 5px;
            }
        }
        
        .job-list {
            height: calc(100% - 20px);
            overflow-y: auto;
            
            &::-webkit-scrollbar {
                width: 2px;
            }
            
            &::-webkit-scrollbar-track {
                background: rgba(3, 4, 74, 0.4);
            }
            
            &::-webkit-scrollbar-thumb {
                background: #2ca8fe;
                border-radius: 1px;
            }
            
            .job-item {
                display: flex;
                align-items: center;
                margin-bottom: 4px;
                padding: 4px 6px;
                background: rgba(3, 4, 74, 0.5);
                border: 1px solid #0D2451;
                border-radius: 3px;
                transition: all 0.3s ease;
                position: relative;
                cursor: pointer;
                
                &:last-child {
                    margin-bottom: 0;
                }
                
                &:hover, &.active-job {
                    background: rgba(44, 168, 254, 0.2);
                    transform: translateX(2px);
                    box-shadow: 0 0 8px rgba(44, 168, 254, 0.3);
                }
                
                .job-rank {
                    width: 18px;
                    height: 18px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    color: #fff;
                    border-radius: 2px;
                    font-size: 11px;
                    margin-right: 5px;
                    flex-shrink: 0;
                    box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
                }
                
                .job-info {
                    flex: 1;
                    overflow: hidden;
                    
                    .job-title {
                        color: #fff;
                        font-size: 13px;
                        white-space: nowrap;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        display: block;
                    }
                    
                    .job-details {
                        margin-top: 3px;
                        font-size: 11px;
                        
                        .detail-row {
                            display: flex;
                            justify-content: space-between;
                            margin-top: 2px;
                            
                            .detail-label {
                                color: #75deef;
                            }
                            
                            .detail-value {
                                color: #feed2c;
                                font-weight: bold;
                            }
                        }
                    }
                }
                
                .job-count {
                    color: #feed2c;
                    font-size: 11px;
                    margin-left: 5px;
                    flex-shrink: 0;
                    background: rgba(254, 237, 44, 0.1);
                    padding: 1px 4px;
                    border-radius: 8px;
                }
            }
        }
    }
    
    .industry-city-panel {
        display: flex;
        flex: 1;
        gap: 5px;
        
        .panel-item {
            flex: 1;
            
            .panel-title {
                font-size: 14px;
                margin-bottom: 5px;
                color: #75deef;
                display: flex;
                align-items: center;
                
                &:before {
                    content: '';
                    display: inline-block;
                    width: 3px;
                    height: 14px;
                    background: linear-gradient(to bottom, #75deef, #2ca8fe);
                    margin-right: 5px;
                }
            }
            
            .panel-content {
                height: calc(100% - 20px);
                overflow-y: auto;
                
                &::-webkit-scrollbar {
                    width: 2px;
                }
                
                &::-webkit-scrollbar-track {
                    background: rgba(3, 4, 74, 0.4);
                }
                
                &::-webkit-scrollbar-thumb {
                    background: #2ca8fe;
                    border-radius: 1px;
                }
                
                .tag-item {
                    display: flex;
                    justify-content: space-between;
                    margin-bottom: 4px;
                    padding: 4px 6px;
                    background: rgba(3, 4, 74, 0.5);
                    border: 1px solid #0D2451;
                    border-radius: 3px;
                    transition: all 0.3s ease;
                    
                    &:last-child {
                        margin-bottom: 0;
                    }
                    
                    &:hover {
                        background: rgba(44, 168, 254, 0.2);
                        transform: translateX(2px);
                    }
                    
                    .tag-name {
                        color: #fff;
                        font-size: 12px;
                        white-space: nowrap;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        max-width: 70%;
                    }
                    
                    .tag-count {
                        color: #feed2c;
                        font-size: 11px;
                        background: rgba(254, 237, 44, 0.1);
                        padding: 1px 4px;
                        border-radius: 8px;
                    }
                }
            }
        }
    }
}

@keyframes stat-border-flow {
    0% {
        transform: translateX(-100%);
    }
    100% {
        transform: translateX(100%);
    }
}
</style>